.banner {
    color: white;
    object-fit: contain;
    height: 448px;
    display: flex;
}

.banner_content{
    margin-top: 7rem;
    margin-left: 30px;
    align-content: center;
}

.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_description{
    width:45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;

}

.banner_buttons {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;
}

.banner_buttons:hover{
    background-color: #fff;
    color: black;
    transition: all 300ms;
}